.header-text {
    text-transform: capitalize;
    font-size: 2rem;
    line-height: 3rem;
    color: rgba(92, 90, 90, 0.896);
}

.header-text h1 {
    color: #fff;
    text-transform: uppercase;
}

.home-btn {
    display: flex;
    gap: 20px;
    margin: 15px 0;
}

.btn h4 {
    font-weight: 500;
}

@media screen and (max-width:992px) {
    .header-text {
        text-align: center;
    }

    header {
        padding-top: 40vh;
    }
}