@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&family=Lobster&family=Rubik+Gemstones&display=swap');

nav {
    background-color: #080808;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1003;
    box-shadow: 0px 0px 32px rgba(255, 255, 255, 0.264);
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 12px 0;
}

.logo h1 {
    font-family: 'Fredoka One', cursive;
    font-weight: 100;
    text-transform: uppercase;
    font-size: 1.8rem;
    color: #4984e5;
}

.nav-links a {
    color: #fff;
    text-decoration: none;
    margin-left: 1.5rem;
    font-size: 1.1rem;
    text-transform: uppercase;
    font-weight: 400;
    position: relative;
}

.nav-links a::after {
    content: "";
    height: 1.5px;
    position: absolute;
    background-color: rgba(255, 255, 255, .25);
    width: 100%;
    bottom: 0;
    left: 0;
    right: 0;
}

.nav-links a::before {
    content: "";
    height: 1.5px;
    position: absolute;
    background-color: #4984e5;
    width: 0%;
    bottom: 0;
    left: 0;
    right: 0;
    transition: 0.4s;
    transform-origin: left;
}

.nav-links a:hover::before {
    width: 100%;
}

/* hamburger */

.hamburger {
    position: relative;
    display: none;
    width: 35px;
    height: 35px;
    cursor: pointer;
    appearance: none;
    background: none;
    outline: none;
    border: none;
    float: right;
}

.hamburger .bar,
.hamburger:after,
.hamburger:before {
    content: '';
    display: block;
    width: 100%;
    height: 5px;
    background-color: #FFF;
    margin: 6px 0px;
    transition: 0.4s;
    border-radius: 2px;
}

.hamburger.is-active:before {
    transform: rotate(-45deg) translate(-8px, 6px);
}

.hamburger.is-active:after {
    transform: rotate(45deg) translate(-9px, -8px);
}

.hamburger.is-active .bar {
    opacity: 0;
}

.mobile-nav {
    position: fixed;
    margin-top: 60px;
    left: 100%;
    width: 100%;
    min-height: 100vh;
    display: block;
    padding-left: 20px;
    transition: 0.4s;
}

.mobile-nav.is-active {
    left: 0%;
}

.mobile-nav a {
    color: #fff;
    font-size: 1.2rem;
    text-decoration: none;
    text-transform: uppercase;
    padding-top: 1rem;
    font-weight: 400;
    display: block;
}

@media screen and (max-width:768px) {
    .hamburger {
        display: block;
    }

    .nav-links {
        display: none;
    }
}