footer {
    box-shadow: 0px 0px 32px rgba(255, 255, 255, 0.264);
    padding: 75px 0 ;
}

.footer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: space-between;
    gap: 25px;
}

#fh {
    font-weight: 600;
    font-size: 22px;
    color: #4984e5;
}

#fbox1 a {
    color: grey;
    font-size: 25px;
    margin-right: 10px;
    transition: 0.4s;
}

#fbox1 a:hover {
    color: #fff;
}

#fbox1 p {
    color: grey;
    transition: 0.4s;
}

#fbox1 p:hover {
    color: #fff;
}

#fbox2 a {
    display: block;
    text-decoration: none;
    color: grey;
    margin-top: 18px;
    transition: 0.4s;
}

#fbox2 a:hover {
    color: #fff;
}

#fbox2 a::before {
    content: "› ";
}

#fbox3 .form {
    display: flex;
    align-items: center;
}

#fbox3 input[type="email"] {
    background-color: transparent;
    outline: none;
    flex-basis: 70%;
    height: 40px;
    border: none;
    color: #fff;
    border: 1px solid white;
    padding-left: 10px;
}

#fbox3 input[type="email"]::placeholder {
    color: #4984e5;
    text-transform: capitalize;
}

#fbox3 input[type="submit"] {
    flex-basis: 30%;
    height: 40px;
    outline: none;
    margin-top: 15px;
    cursor: pointer;
    border: 1px solid white;
}

@media screen and (max-width:992px) {
    .footer {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr;
    }
}