main {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding: 0 15px ;
}

.btn {
    cursor: pointer;
    outline: none;
    background-color: transparent;
    border: 1px solid rgba(92, 90, 90, 0.896);
    padding: 8px 32px;
    text-transform: uppercase;
    color: #fff;
    font-size: 16px;
    display: flex;
    align-items: center;
    gap: 5px;
    box-sizing: border-box;
    transition: 0.4s;
    color: #4984e5;
}

.btn:hover {
    color: #000;
    background-color: #fff;
}

.mh1 {
    text-transform: capitalize;
}

.mt1 {
    margin-top: 15px;
}

.mt2 {
    margin-top: 25px;
}

