.contact {
    padding: 75px 0;
}

.row span {
    border: 1px solid #4984e5;
    border-radius: 50%;
    height: 60px;
    width: 60px;
    display: flex;
    justify-content: center;
    padding-top: 17px;
}

.row {
    display: flex;
    justify-content: space-between;
}

.cols {
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 12px;
}

.contact-form {
    display: flex;
    flex-direction: column;
    max-width: 52rem;
    margin: 10vh auto 0;
    gap: 20px;
}

.contact input,
textarea {
    display: block;
    width: 100%;
    padding-top: 1.1rem;
    padding-bottom: 0.1rem;
    padding-left: 1.5rem;
    border: 1px solid grey;
    background-color: transparent;
    color: #4984e5;
    font-size: 0.8rem;
    line-height: 1.8rem;
    transition: all 0.2s;
}

.contact button {
    color: #fff;
    background-color: #4984e5;
    height: 50px;
    border: none;
    font-weight: 600;
    font-size: 0.850rem;
    transition: 0.2s;
}

.contact-form button:hover {
    opacity: 0.7;
}

.contact input:focus {
    outline: none;
    border: 1px solid #4d5055;
}

.contact textarea:focus {
    outline: none;
    border: 1px solid #4d5055;
}

.contact input:focus::placeholder {
    color: #fff;
    font-weight: 600;
    position: absolute;
    margin-top: -13px;
    transition: 0.2s;
}

.contact textarea:focus::placeholder {
    color: #fff;
    font-weight: 600;
    position: absolute;
    margin-top: -17px;
    transition: 0.2s;
}

@media screen and (max-width:768px) {
    .row {
        flex-direction: column;
        justify-content: baseline;
        align-items: baseline;
        gap: 20px;
    }
}