.hs {
    background-color: #121212;
    padding: 75px 0 50px 0;
}

.h1 {
    font-size: 40px;
    position: relative;
}

.h1::after {
    content: "";
    background-color: #4984e5;
    position: absolute;
    height: 3px;
    border-radius: 8px;
    width: 60px;
    left: 0;
    bottom: 0;
}

#home-services {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 50px;
    align-items: baseline;
}

.box1 {
    grid-column: 1/2;
    grid-row: 1/3;
}

.box1 a {
    width: max-content;
    text-decoration: none;
}

#home-services h2 {
    font-weight: 500;
}

.p {
    color: grey;
    text-transform: capitalize;
}

#home-services span {
    color: #fff;
    font-size: 1.5rem;
    border: 2px solid #4984e5;
    padding: 15px 15px 5px 15px;
    border-radius: 8px;
}

.box4 a {
    text-decoration: none;
    width: max-content;
}

.ha {
    background-color: #080808;
}

.home-about {
    padding: 75px 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    gap: 0 75px;
    align-items: center;
}

#circle {
    width: 100px;
    height: 100px;
    background: #4984e5;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    float: left;
}

.ha-box2 {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
}

.ha-grid-box {
    grid-column: 1/3;
    grid-row: 1/2;
}

.ha-gbox-same {
    display: flex;
    align-items: center;
    gap: 20px;
}

.ha-gbox-same h2 {
    font-weight: 500;
    font-size: 20px;
}

/* customer */

.customer-views {
    padding: 75px 0;
    background-color: #141414;
}

.customer-col {
    flex: 0 0 33%;
    display: flex;
    flex-direction: column;
}

.customer-row {
    display: flex;
    flex-direction: row;
    gap: 30px;
    margin-bottom: 2rem;
    margin-left: -30px;
    width: 100%;
}

.customer-head-row {
    max-width: 40rem;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    margin-bottom: 3rem;
}

.customer-head-row h1 {
    color: #fff;
    font-weight: 600;
    font-size: 40px;
    margin-bottom: 1.5rem;
    line-height: 2.5rem;
}

.customer-col .col-para {
    font-size: 0.9rem;
    color: grey;
    font-weight: 400;
    margin-bottom: 20px;
    line-height: 1.5rem;
    text-align: center;
}

.customer-col .test-col img {
    height: 60px;
    width: 60px;
    border-radius: 50%;
    float: left;
}

.customer-col .test-col .text {
    margin: 10px 80px;
}

.customer-col .test-col .text h1 {
    font-size: 0.9rem;
    font-weight: 700;
    color: #4984e5;
}

.customer-col .test-col .text h2 {
    font-size: 0.9rem;
    color: #fff;
    font-weight: 400;
}

.customer-col .test-col {
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-self: center;
    justify-self: center;
}

.customer-col .test-col .text {
    text-align: center;
    margin: 10px none;
}

@media screen and (max-width:992px) {

    .home-about {
        flex-direction: column;
    }

    .ha-box2 {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
    }

    .ha-grid-box {
        grid-column: 1/1;
        grid-row: 1/2;
        text-align:center;
        margin-top: 15px;
    }

    .h1::after {
        display: none;
    }

    .ha-gbox-same {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 20px;
    }

    #home-services {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr;
    }

    .box1 {
        grid-column: 1/2;
        grid-row: 1/2;
    }

    .gbox {
        justify-content: center;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .p {
        text-align:center;
    }

    .h1 {
        text-align: center;
    }

    .btn {
        margin: 10px auto 0 auto;
    }

    
    .customer-col .test-col {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-self: center;
        justify-self: center;
    }

    .customer-col {
        margin-right: -30px;
        padding-left: 15px;
    }

    .customer-row {
        flex-direction: column;
    }

    .customer-col .test-col .text {
        text-align: center;
        margin: 10px none;
    }

    .customer-col .col-para {
        text-align: center;
    }

} 

